html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

body {
  font-family: 'Open Sans', sans-serif;
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  overflow-x: hidden;
  width: 100%;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.container {
  margin: 0 5% 0 5%;
  padding: 0 15px;
  position: relative;
  min-width: 300px;
}

.header {
  z-index: 20;
  position: absolute;
  width: 100%;
  color: #ffffff;
  padding-top: 50px;
}

.header .container {
  display: flex;
  justify-content: space-between;
}

.header .logo {
  position: relative;
  height: 125px;
}

.header .logo_img img {
  height: 100%;
}

.header .logo h1 {
  font-size: 14px;
  font-weight: 300;
}

.header .logo_text {
  position: absolute;
  right: -280px;
  bottom: 0;
  color: #ccc;
}

.header_phone {
  position: relative;
}

.header_phone a {
  vertical-align: top;
  color: #ffffff;
  font-size: 21px;
}

.header_phone a span {
  font-weight: 700;
  vertical-align: top;
}

@media only screen and (max-width: 1200px) {
  .header_phone {
    width: 0;
  }
  .header_phone a {
    width: 250px;
    right: -20px;
    vertical-align: bottom;
    bottom: 0;
    position: absolute;
  }
  .header_phone a span {
    vertical-align: bottom;
  }
}

.header_menu {
  font-size: 16px;
}

.header_menu ul {
  display: flex;
  width: 700px;
  justify-content: space-betweens;
}

.header_menu ul li a {
  color: #ffffff;
}

@media only screen and (max-width: 950px) {
  .header_menu {
    display: none;
  }
}

@media only screen and (max-width: 900px) {
  .header .logo_text {
    right: -240px;
  }
  .header .logo h1 {
    font-size: 12px;
  }
  .header_phone a {
    width: 210px;
    font-size: 16px;
  }
  .header_phone a span {
    vertical-align: sub;
    font-size: 24px;
  }
}

@media only screen and (max-width: 700px) {
  .header_phone {
    display: none;
  }
  .header .logo_text {
    margin-left: -10px;
    right: -70px;
    bottom: -70px;
  }
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

.sidebar {
  z-index: 30;
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  color: #ffffff;
  width: 74px;
  background-image: url(../img/nav-bg.png);
  background-repeat: repeat;
}

.sidebar .menu {
  position: relative;
  width: 100%;
  text-align: center;
}

.sidebar .menu_burger {
  margin: 40px auto;
  width: 30px;
  cursor: pointer;
}

.sidebar .menu .line {
  text-align: center;
  border-bottom: 3px solid #fff;
  margin-bottom: 5px;
  width: 30px;
}

.section {
  background-position: center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  padding: 10vh;
  height: 100vh;
  width: 100%;
  vertical-align: center;
  background-color: #000000;
  color: #ffffff;
  opacity: 0;
  transition: all .5s ease;
}

.section:nth-child(odd) {
  background-color: #00d0a0;
}

@media only screen and (max-width: 902px) {
  .section:nth-child(odd) {
    position: relative;
    opacity: 1 !important;
  }
  .section:nth-child(odd) [data-stagger] {
    opacity: 1 !important;
  }
}

.is-active {
  opacity: 1;
}

.left-content {
  position: absolute;
  width: 500px;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: blue;
}

@media only screen and (max-width: 1000px) {
  .left-content {
    width: 100vw;
    position: relative;
    height: 300px;
  }
}

.content {
  width: 100vw;
  height: 100vh;
  background-color: rgba(142, 133, 12, 0.7);
}

@media only screen and (max-width: 902px) {
  .content {
    position: relative;
  }
}
